in: 100% 100%;\n    }\n  }\n\n\n  &_popover {\n    pointer-events: none;\n\n    &-active {\n      pointer-events: auto;\n    }\n\n    &-active &_panel {\n      opacity: 1;\n      transform: translateY(0);\n    }\n\n    &_panel {\n      position: absolute;\n      bottom: 100%;\n      opacity: 0;\n      right: 0;\n      margin-bottom: 5px;\n      padding: 3px 0;\n      font-size: 13px;\n      white-space: nowrap;\n      overflow: hidden;\n      transform: translateY(10px);\n      background: rgba(0, 0, 0, .7);\n      box-shadow: 0 1px 20px 1px rgba(0,0,0,.2);\n      z-index: 2;\n      transition: opacity .2s ease, transform .2s ease, height .2s cubic-bezier(.4,0,.2,1), width .2s cubic-bezier(.4,0,.2,1);\n\n      &-left {\n        left: 0;\n        right: auto;\n      }\n    }\n\n    &_mask {\n      position: fixed;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      top: 0;\n      z-index: 1;\n    }\n  }\n\n  &_switch {\n    display: inline-block;\n    width: 30px;\n    padding: 2px;\n    border-radius: 10px;\n    background: rgba(255, 255, 255, .3);\n    transition: background-color .3s cubic-bezier(.4, 0, .2, 1);\n    cursor: pointer;\n    user-select: none;\n\n    &::after {\n      content: '';\n      display: block;\n      width: 15px;\n      height: 15px;\n      border-radius: 50%;\n      background: #fff;\n      box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,.14), 0px 1px 3px 0px rgba(0,0,0,.12);\n      transition: transform .15s cubic-bezier(.4, 0, .2, 1);\n    }\n\n    &-active {\n      background: #448AFF;\n\n      &::after {\n        transform: translateX(16px);\n      }\n    }\n  }\n\n  &_loading {\n    display: none;\n    z-index: 15;\n    pointer-events: none;\n\n    div {\n      position: absolute;\n      left: 50%;\n      top: 50%;\n      width: 50px;\n      height: 50px;\n      border: 6px solid transparent;\n      border-top-color: #fff;\n      border-radius: 50%;\n      animation: rplayer-loading 1.2s cubic-bezier(.5, 0, .5, 1) infinite;\n    }\n    div:nth-child(1) { animation-delay: -.45s; }\n    div:nth-child(2) { animation-delay: -.3s; }\n    div:nth-child(3) { animation-delay: -.15s; }\n  }\n\n  &_toast {\n    position: absolute;\n    padding: 3px 5px;\n    font-size: 12px;\n    background: rgba(0, 0, 0, .7);\n\n    $pad-size: 5px;\n\n    &-center {\n      left: 50%;\n      right: 50%;\n      transform: translate(-50%, -50%);\n    }\n\n    &-left-top, &-right-top {\n      top: $pad-size;\n    }\n\n    &-left-top, &-left-bottom {\n      left: $pad-size;\n    }\n\n    &-right-top, &-right-bottom {\n      right: $pad-size;\n    }\n\n    &-left-bottom, &-right-bottom {\n      bottom: 60px;\n    }\n  }\n\n  &_slider {\n    display: inline-block;\n    flex: 1;\n    margin: 0 6px;\n    padding: 5px 0;\n    position: relative;\n    cursor: pointer;\n    user-select: none;\n\n    &_track {\n      width: 100%;\n      height: 2px;\n      overflow: hidden;\n      background: rgba(255, 255, 255, 0.3);\n\n      &_inner {\n        height: 100%;\n        transform-origin: 0 0;\n        background: #448AFF;\n      }\n    }\n\n    &_stop {\n      position: absolute;\n      width: 3px;\n      height: 6px;\n      top: 3px;\n      white-space: nowrap;\n      font-size: 12px;\n      background: rgba(255, 255, 255, 0.7);\n\n      span {\n        position: absolute;\n        left: 2px;\n        top: 6px;\n        transform: translateX(-50%);\n      }\n\n    }\n\n    &_dot {\n      position: absolute;\n      top: 0;\n      left: -6px;\n      width: 12px;\n      height: 12px;\n      border-radius: 50%;\n      pointer-events: none;\n      background: #448AFF;\n    }\n\n  }\n\n  &_checkbox {\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    font-size: 12px;\n    color: #fff;\n    cursor: pointer;\n    user-select: none;\n    transition: color .2s ease;\n\n    &::before {\n      content: '';\n      display: block;\n      width: 1em;\n      height: 1em;\n      border: 1px solid currentColor;\n      border-radius: 2px;\n      margin-right: 5px;\n      transition: background .2s ease;\n    }\n\n    &::after {\n      content: '';\n      position: absolute;\n      opacity: 0;\n      left: 0.5em;\n      top: 50%;\n      width: 4px;\n      height: 7px;\n      border: 1px solid #fff;\n      border-top: 0;\n      border-left: 0;\n      transform: translate(-30%, -70%) rotate(45deg);\n    }\n\n    &:hover {\n      color: #448AFF;\n    }\n\n    &-active {\n      &::before {\n        background: #448AFF;\n        border-color: #448AFF;\n      }\n\n      &::after {\n        opacity: 1;\n      }\n    }\n  }\n\n  &_control {\n    position: absolute;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    z-index: 10;\n    user-select: none;\n    padding: 0 5px;\n    transition: opacity .2s ease;\n\n    &-hide {\n      opacity: 0;\n      pointer-events: none;\n    }\n\n    &_bg {\n      position: absolute;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      height: 100px;\n      background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAADGCAYAAAAT+OqFAAAAdklEQVQoz42QQQ7AIAgEF/T/D+kbq/RWAlnQyyazA4aoAB4FsBSA/bFjuF1EOL7VbrIrBuusmrt4ZZORfb6ehbWdnRHEIiITaEUKa5EJqUakRSaEYBJSCY2dEstQY7AuxahwXFrvZmWl2rh4JZ07z9dLtesfNj5q0FU3A5ObbwAAAABJRU5ErkJggg==) repeat-x bottom;\n      pointer-events: none;\n      z-index: 9;\n      transition: opacity .2s ease;\n\n      &-hide {\n        opacity: 0;\n      }\n    }\n\n    &_bar {\n      display: flex;\n      align-items: center;\n      font-size: 0;\n\n      > div {\n        margin: 3px 2px;\n      }\n    }\n\n    &_time {\n      font-size: 13px;\n      margin-left: 8px;\n      margin-right: auto;\n      margin-top: -1px;\n      text-shadow: 0 0 2px rgba(0,0,0,.3);\n      white-space: nowrap;\n      letter-spacing: 1px;\n\n      :first-child::after {\n        content: '/';\n        display: inline-block;\n        font-size: 12px;\n        margin: 0 5px;\n        transform: translateY(-0.5px);\n      }\n    }\n\n    &_volume {\n      position: relative;\n      z-index: 1;\n\n      &_bars, &_bar {\n        position: absolute;\n        top: 0;\n        bottom: 0;\n      }\n\n      &_bars {\n        left: 100%;\n        font-size: 0;\n        background: rgba(255, 255, 255, .3);\n        opacity: 0;\n        pointer-events: none;\n        overflow: hidden;\n        transition: opacity .2s ease;\n      }\n\n      &:hover &_bars {\n        opacity: 1;\n        transform: translateX(0);\n        pointer-events: auto;\n      }\n\n      &_bar {\n        left: 0;\n        right: 0;\n        transform-origin: 0 0;\n        transform: scaleX(0);\n        will-change: transform;\n      }\n    }\n\n    &_setting {\n\n      .rplayer_icon {\n        transition: transform .2s ease;\n      }\n\n      &-active {\n        background: #448AFF;\n\n        .rplayer_icon {\n          transform: rotate(90deg);\n        }\n      }\n\n      &_item, &_option {\n        display: flex;\n        align-items: center;\n        padding: 5px 8px;\n\n        &:hover {\n          background: rgba(255, 255, 255, .2);\n        }\n      }\n\n      &_item {\n        justify-content: space-between;\n\n        & > :first-child {\n          margin-right: 20px;\n        }\n\n        &-select {\n          &::after {\n            content: '';\n            display: block;\n            width: 0;\n            height: 0;\n            margin-left: 10px;\n            border: 4px solid transparent;\n            border-left-color: #fff;\n          }\n        }\n      }\n\n      &_option {\n\n        &::before {\n          content: '';\n          opacity: 0;\n          margin-left: 6px;\n          margin-right: 17px;\n          @include check-mark;\n        }\n\n        &-active {\n          &::before {\n            opacity: 1;\n          }\n        }\n      }\n\n      &_back {\n        justify-content: flex-start;\n        padding-right: 20px;\n        border-bottom: 1px solid rgba(255, 255, 255, .2);\n\n        &::before {\n          content: '';\n          display: block;\n          width: 0;\n          height: 0;\n          margin-right: 21px;\n          border: 4px solid transparent;\n          border-right-color: #fff;\n        }\n      }\n    }\n  }\n\n  &_contextmenu {\n    position: absolute;\n    top: 0;\n    left: 0;\n    padding: 3px 0;\n    font-size: 13px;\n    background: rgba(0, 0, 0, .7);\n    z-index: 20;\n\n    &_item {\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      padding: 5px 8px;\n      white-space: nowrap;\n      cursor: pointer;\n      user-select: none;\n\n      &:hover {\n        background: rgba(255, 255, 255, .2);\n      }\n\n      &-disabled {\n        color: rgba(255, 255, 255, .5);\n\n        &:hover {\n          background: transparent;\n        }\n      }\n\n      &-checked {\n        &::after {\n          content: '';\n          @include check-mark;\n          margin: 0 2px 5px 2px\n        }\n      }\n    }\n  }\n\n  &_progress {\n    position: relative;\n    cursor: pointer;\n\n    &:hover &_bars {\n      height: 16px;\n    }\n\n    &_bars {\n      position: relative;\n      height: 4px;\n      background: rgba(255, 255, 255, .3);\n      touch-action: none;\n      transition: height .12s ease;\n\n      &::after {\n        content: '';\n        position: absolute;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        height: 16px;\n      }\n    }\n\n    &_played, &_buf {\n      position: absolute;\n      left: 0;\n      right: 0;\n      top: 0;\n      bottom: 0;\n      transform-origin: 0 0;\n    }\n\n    &_played {\n      transform: scaleX(0);\n      will-change: transform;\n    }\n\n    &_hover {\n      opacity: 0;\n      background: rgba(255, 255, 255, .5);\n    }\n  }\n\n  &_progress:hover &_thumb {\n    display: block;\n  }\n\n  &_thumb {\n    display: none;\n    position: absolute;\n    left: 0;\n    bottom: 20px;\n    font-size: 12px;\n    text-align: center; \n    pointer-events: none;\n    text-shadow: 0 0 2px rgba(0,0,0,.3);\n\n    &_img {\n      border: 1px solid #fff;\n      box-shadow: 0 0 10px rgba(0,0,0,0.1);\n      background-color: rgba(255, 255, 255, .5);\n      background-repeat: no-repeat;\n    }\n\n    &_time {\n      display: inline-block;\n      margin-top: 5px;\n      padding: 2px 5px 1px;\n      border-radius: 3px;\n      background: rgba(0, 0, 0, .7);\n    }\n  }\n}\n\n@keyframes rplayer-loading {\n  0% {\n    transform: translate(-50%,-50%) rotate(0deg);\n  }\n  100% {\n    transform: translate(-50%,-50%) rotate(360deg);\n  }\n}\n","@mixin check-mark($w: 5px, $h: 12px, $borderWidth: 1px, $color: #fff) {\n  display: inline-block;\n  transform: rotate(45deg);\n  height: $h;\n  width: $w;\n  border-bottom: $borderWidth solid $color;\n  border-right: $borderWidth solid $color;\n}\n"],"sourceRoot":""}]);
